import { PureComponent } from 'react';
import { CategoryTreeType } from 'Type/Category';

/**
 * Category types
 * @class CategoryTypes
 * @namespace Component/CategoryTypes/Component
 */
export class CategoryTypes extends PureComponent {
    static propTypes = {
        category: CategoryTreeType.isRequired
    };
    renderCategoryType(){
        const {category: {category_type}} = this.props
        return(
            <div>{category_type}</div>
        )
    }

    render(){
        return(
            <div style={{height: "41px", backgroundColor: "red"}}>{ this.renderCategoryType() }</div>
        )

    }
}

export default CategoryTypes;